"use client";

import { useState } from "react";
import styles from "./EventDetails.module.css";
import CalendarModal from "./CalendarModal";

export default function EventDetails({ data, setData, onBack, onNext }) {
  const [mode, setMode] = useState("online");
  const [showCalendar, setShowCalendar] = useState(false);

  return (
    <>
      <div className={styles.card}>
        <h3 className={styles.cardTitle}>Primary Details</h3>

        <div className={styles.grid3}>
          <input
            className={styles.input}
            placeholder="Event Title"
            value={data.title || ""}
            onChange={(e) => setData({ ...data, title: e.target.value })}
          />

          <select
            className={styles.input}
            value={data.category || ""}
            onChange={(e) => setData({ ...data, category: e.target.value })}
          >
            <option value="">Select Category</option>
            <option>Technical</option>
            <option>Cultural</option>
          </select>

          <select
            className={styles.input}
            value={data.eventType || ""}
            onChange={(e) => setData({ ...data, eventType: e.target.value })}
          >
            <option value="">Select Event Type</option>
            <option>Workshop</option>
            <option>Hackathon</option>
          </select>
        </div>

        <textarea
          className={styles.textarea}
          placeholder="About Event"
          value={data.about || ""}
          onChange={(e) => setData({ ...data, about: e.target.value })}
        />
      </div>

      <div className={styles.card}>
        <div className={styles.cardHeader}>
          <h3>Calendar</h3>
          <button
            className={styles.btnPrimary}
            onClick={() => setShowCalendar(true)}
          >
            + Add
          </button>
        </div>
      </div>

      <div className={styles.actions}>
        <button className={styles.btnOutline} onClick={onBack}>
          Back
        </button>
        <button className={styles.btnPrimary} onClick={onNext}>
          Next
        </button>
      </div>

      {showCalendar && (
        <CalendarModal
          onClose={() => setShowCalendar(false)}
          onSave={(rows) => {
            setData({ ...data, calendar: rows });
            setShowCalendar(false);
          }}
        />
      )}
    </>
  );
}
