"use client";

import { useState } from "react";
import styles from "./CreateEvent.module.css";

import Stepper from "./components/Stepper";
import OrganizerDetails from "./steps/OrganizerDetails";
import EventDetails from "./steps/EventDetails";
import MediaTickets from "./steps/MediaTickets";

import { getUserData } from "@/lib/auth";

export default function CreateEvent() {
  const user = getUserData();

  if (user?.type !== "org") {
    return <p style={{ padding: 30 }}>Only organizers can create events</p>;
  }

  const [step, setStep] = useState(1);

  const [organizerData, setOrganizerData] = useState({});
  const [eventData, setEventData] = useState({});
  const [mediaData, setMediaData] = useState({});

  return (
    <div className={styles.wrapper}>
      <Stepper step={step} />

      {step === 1 && (
        <OrganizerDetails
          data={organizerData}
          setData={setOrganizerData}
          onNext={() => setStep(2)}
        />
      )}

      {step === 2 && (
        <EventDetails
          data={eventData}
          setData={setEventData}
          onBack={() => setStep(1)}
          onNext={() => setStep(3)}
        />
      )}

      {step === 3 && (
        <MediaTickets
          data={mediaData}
          setData={setMediaData}
          onBack={() => setStep(2)}
          onSubmit={() => {
            console.log("FINAL PAYLOAD", {
              organizerData,
              eventData,
              mediaData,
            });
            alert("Event Created Successfully");
            setStep(1);
          }}
        />
      )}
    </div>
  );
}
