"use client";

import styles from "./OrganizerDetails.module.css";

export default function OrganizerDetails({ data, setData, onNext }) {
  const organizations = data.organizations || [
    {
      hostBy: "",
      orgName: "",
      location: "",
      organizerName: "",
      organizerNumber: "",
      department: "",
    },
  ];

  const updateOrg = (index, key, value) => {
    const updated = [...organizations];
    updated[index][key] = value;
    setData({ ...data, organizations: updated });
  };

  const addOrganization = () => {
    setData({
      ...data,
      organizations: [
        ...organizations,
        {
          hostBy: "",
          orgName: "",
          location: "",
          organizerName: "",
          organizerNumber: "",
          department: "",
        },
      ],
    });
  };

  const deleteOrganization = (index) => {
    setData({
      ...data,
      organizations: organizations.filter((_, i) => i !== index),
    });
  };

  return (
    <>
      <div className={styles.card}>
        <h3 className={styles.cardTitle}>Hosting Information</h3>

        {organizations.map((org, index) => (
          <div key={index} className={styles.orgBlock}>
            <div className={styles.orgHeader}>
              <span>Organization {index + 1}</span>

              {index !== 0 && (
                <button
                  className={styles.deleteBtn}
                  onClick={() => deleteOrganization(index)}
                >
                  Delete
                </button>
              )}
            </div>

            <div className={styles.field}>
              <label>Event Host By <span>*</span></label>
              <select
                className={styles.input}
                value={org.hostBy}
                onChange={(e) =>
                  updateOrg(index, "hostBy", e.target.value)
                }
              >
                <option value="">Select Category</option>
                <option>College</option>
                <option>Company</option>
                <option>Community</option>
              </select>
            </div>

            <div className={styles.grid2}>
              <div className={styles.field}>
                <label>Organization Name <span>*</span></label>
                <input
                  className={styles.input}
                  value={org.orgName}
                  onChange={(e) =>
                    updateOrg(index, "orgName", e.target.value)
                  }
                />
              </div>

              <div className={styles.field}>
                <label>Location <span>*</span></label>
                <input
                  className={styles.input}
                  value={org.location}
                  onChange={(e) =>
                    updateOrg(index, "location", e.target.value)
                  }
                />
              </div>

              <div className={styles.field}>
                <label>Organizer Name <span>*</span></label>
                <input
                  className={styles.input}
                  value={org.organizerName}
                  onChange={(e) =>
                    updateOrg(index, "organizerName", e.target.value)
                  }
                />
              </div>

              <div className={styles.field}>
                <label>Organizer Number <span>*</span></label>
                <input
                  className={styles.input}
                  value={org.organizerNumber}
                  onChange={(e) =>
                    updateOrg(index, "organizerNumber", e.target.value)
                  }
                />
              </div>
            </div>

            <div className={styles.field}>
              <label>Department <span>*</span></label>
              <select
                className={styles.input}
                value={org.department}
                onChange={(e) =>
                  updateOrg(index, "department", e.target.value)
                }
              >
                <option value="">Select Department</option>
                <option>CSE</option>
                <option>ECE</option>
                <option>IT</option>
                <option>Management</option>
              </select>
            </div>
          </div>
        ))}

        <div className={styles.addWrap}>
          <button className={styles.addBtn} onClick={addOrganization}>
            + Add Collaborators
          </button>
        </div>
      </div>

      <div className={styles.actionEnd}>
        <button className={styles.nextBtn} onClick={onNext}>
          Next
        </button>
      </div>
    </>
  );
}
